  top: 0;
  left: 0;
  height: 5.625rem;
  transition: 0.3s;
}

/* スクロールしたらヘッダーメニューのフォントカラー変更 */
.header.headerColorScroll {
  background-color: #fff;
  transition: color 0.4s ease-out;
  transition: 0.3s;
}

.header__inner {
  width: 100%;
  padding: 0 1rem;
  display: flex;
  margin: 0 auto;
  height: inherit;
}

.header__logo {
  height: inherit;
  width: 6.375rem;
}
@media screen and (min-width: 768px) {
  .header__logo {
    width: 8.3125rem;
  }
}

.header__logo a {
  height: inherit;
  display: flex;
  align-items: center;
}

.header__nav {
  height: inherit;
  margin-left: auto;
}

.header-nav__items {
  color: #fff;
  height: inherit;
}

.header-nav__item {
  position: relative;
  font-size: 1.125rem;
  font-weight: 400;
  font-family: "Gotu", sans-serif;
  height: inherit;
  text-align: center;
  height: inherit;
  text-transform: capitalize;
  height: inherit;
}

.header-nav__item--faq {
  text-transform: uppercase;
}

.header-nav__item a {
  padding-left: 1.28125rem;
  padding-right: 1.28125rem;
  padding-bottom: 1rem;
  height: inherit;
  align-items: center;
  display: flex;
}

.header-nav__item a::after {
  position: absolute;
  left: 0;
  content: "";
  width: 100%;
  height: 0.0625rem;
  background: #fff;
  bottom: 0.625rem;
  /*アンダーラインがaタグの下端から現れる*/
  transform: scale(0, 1);
  /*アンダーラインの縮尺比率。ホバー前はx方向に0*/
  transform-origin: 68px -61px;
  /*変形（アンダーラインの伸長）の原点がaタグ（各メニュー）の左端*/
  transition: transform 0.3s;
  /*変形の時間*/
}

.header-nav__item a:hover:after {
  transform: scale(0.9, 0.9);
}

.header-nav__item span {
  position: absolute;
  content: "";
  top: 66%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  font-size: 0.625rem;
}

.header-nav__items.u-desktop {
  display: flex;
  height: inherit;
}

.header__hamburger {
  position: fixed;
  z-index: 9999;
  top: 0;
  right: 0.9375rem;
  width: 2rem;
  height: inherit;
  cursor: pointer;
}

.header__hamburger.is-active {
  right: 1rem;
  height: inherit;
}

.header__hamburger span {
  position: absolute;
  left: 0;
  display: inline-block;
  width: 100%;
  height: 2px;
  transition: transform 0.5s;
  background-color: #fff;
  border-radius: 2px;
}

.header__hamburger span:first-child {
  top: 2rem;
}

.header__hamburger span:nth-child(2) {
  top: 2.75rem;
  transition: opacity 0.3s;
}

.header__hamburger span:nth-child(3) {
  top: 3.5rem;
}

.header__hamburger.is-active span:first-child {
  top: 0.8125rem;
  background-color: #fff;
  transform: translateY(20px) rotate(39deg);
}

.header__hamburger.is-active span:nth-child(2) {
  opacity: 0;
}

.header__hamburger.is-active span:nth-child(3) {
  top: 3.3125rem;
  background-color: #fff;
  transform: translateY(-20px) rotate(-39deg);
}

